import React from 'react';
import {getLang} from "@web3game/lang";
import {Page} from "react-onsenui";
import {PagePadder} from "@web3game/common/components/PagePadder";
import {compose} from "@web3game/common/utils/compose";
import {hot} from "react-hot-loader";
import {createHeaderRender} from "../Header";


export function AboutUSPagePure() {
    const renderFixed = createHeaderRender(getLang().HOME_PAGE.ABOUT_US);

    return (
        <Page renderFixed={renderFixed}>
            <PagePadder />
            <div style={{ padding: 10 }}>
                {getLang().HOME_PAGE.ABOUT_US_CONTENT}
                <br />
            </div>
        </Page>
    );
}

export const AboutUSPage = compose(
    hot(module),
    React.memo,
)(AboutUSPagePure);

export default AboutUSPage;
